<!-- tab -->
<template>
    <div class="live-tab" :style="{justifyContent: center ? 'center' : 'flex-end'}">
        <div @click="handleClick(item, i)" class="live-tab-item" :class="activeIndex==i?'active-live-item':''" v-for="(item, i) in list" :key="i">{{item.name}}</div>
    </div>
</template>

<script>
export default {
    props: {
        list: Array,
        center: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            activeIndex: 0
        }
    },
    methods: {
        handleClick (item, i) {
            this.activeIndex = i
            this.$emit('clickTab', item)
        }
    }
}
</script>

<style scoped>
.live-tab {
    width: 100%;
    margin-top: 26px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;
    user-select: none;
}
.live-tab-item {
    width: 160px;
    height: 50px;
    margin-left: 12px;
    background-image: url("../assets/primary-medium-txt-normal.png");
    background-size: 100% 100%;
    font-size: 18px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
}
.live-tab-item:hover {
     background-image: url("../assets/primary-medium-new-selected.png");
     height: 66px;
     width: 180px;
     margin-left: -8px;
     color: #fff;
     position: relative;
     left: 8px;
}
.active-live-item {
    background-image: url("../assets/primary-medium-new-selected.png");
    height: 66px;
    width: 180px;
    margin-left: -8px;
    color: #fff;
    position: relative;
    left: 8px;
}
</style>