<!--  -->
<template>
    <div class="home-server">
        <HomeHeaderWings :img="imgs.server" :title="''" :content="''" />
        <div class="server-nums" ref="nums">
            <div class="server-num" v-for="(item, i) in list" :key="i">
                <div class="server-item">
                    <b>{{item.num}}</b><span>{{item.tail}}</span>
                    <div class="server-item-bg"></div>
                    <div class="server-item-mask"></div>
                    <div class="server-item-mask server-item-mask2" :class="key?'active-mask':''" :style="{transform:key?`rotate(${item.deg}deg)`:'rotate(180deg)'}"></div>
                </div>
                <div class="server-title">{{item.title}}</div>
                <div class="server-info">{{item.info}}</div>
            </div>
        </div>
        <div class="server-exp">
            <div class="server-exp-item" v-for="(item, i) in exps" :key="i">
                <div class="exp-icon">
                    <img :src="item.icon" alt="icon">
                </div>
                <div class="exp-info">
                    <b class="exp-title">{{item.title}}</b>
                    <div class="exp-text">{{item.info}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import HomeHeaderWings from './HomeHeaderWings'
import server from '../assets/quality-services.png'
import Icon1 from '../assets/icon-fast-payment.png'
import Icon2 from '../assets/icon-quantity-games.png'
import Icon3 from '../assets/icon-safe-security.png'
import Icon4 from '../assets/icon-all-platform.png'

export default {
    data () {
        return {
            key: false,
            imgs: {
                server: server
            },
            list: [
                {
                    title: '',
                    info: 'AVERAGE TIME OF DEPOSIT',
                    num: 60,
                    tail: '',
                    deg: 45
                },
                {
                    title: '',
                    info: 'PAYMENT PLATFORM PARTNERS',
                    num: 80,
                    tail: '',
                    deg: 15
                },
                {
                    title: '',
                    info: 'AVERAGE TIME OF WITHDRAW',
                    num: 90,
                    tail: '',
                    deg: 0
                },
                {
                    title: '',
                    info: 'GAMING PROVIDER PARTNERS',
                    num: 23,
                    tail: '',
                    deg: 80
                }
            ],
            exps: [
                {
                    icon: Icon1,
                    title: '',
                    info: ''
                },
                {
                    icon: Icon2,
                    title: '',
                    info: ''
                },
                {
                    icon: Icon3,
                    title: '',
                    info: '128'
                },
                {
                    icon: Icon4,
                    title: '',
                    info: 'WebH5iOSAndroidAPP7x24'
                }
            ]
        }
    },
    components: {
        HomeHeaderWings
    },
    mounted () {
        const callback = entries => {
            const rs = entries[0].intersectionRatio
            this.key = !!(rs)
        }
        const observer = new IntersectionObserver(callback)
        observer.observe(this.$refs.nums)
    }
}
</script>

<style scoped>
.home-server {
    width: 100%;
    margin-top: 50px;
}
.server-nums {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.server-num {
    width: 130px;
    height: 220px;
    text-align: center;
}
.server-item {
    width: 130px;
    height: 130px;
    background-image: url("../assets/image-table.png");
    background-size: 100% 100%;
    position: relative;
    color: #fff;
    line-height: 130px;
}
.server-item b {
    font-size: 32px;
}
.server-item span {
    font-size: 14px;
}
.server-title {
    margin-top: 20px;
    font-size: 18px;
    font-weight: bold;
    color: #424a66;
}
.server-info {
    width: 128px;
    line-height: 1.4;
    margin: 10px auto;
    font-size: 12px;
    color: #67718f;
}
.server-item-bg {
    background-size: 100% 100%;
    background-image: url("../assets/image-scale-active.png");
    width: 90px;
    height: 90px;
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
}
.server-item-mask {
    background-size: 200% 100%;
    background-image: url("../assets/image-scale-default-all.png");
    width: 45px;
    height: 90px;
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 2;
}
.server-item-mask2 {
    transform-origin: 100% 50%;
    transform: rotate(180deg);
}
.active-mask {
    transition: all 2s linear;
}
.server-exp {
    width: 100%;
    height: 282px;
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: space-between;
}
.server-exp-item {
    width: 540px;
    height: 106px;
    display: flex;
    flex-direction: row;
}
.exp-icon {
    width: 106px;
    height: 106px;
}
.exp-icon img {
    width: 100%;
    height: 100%;
}
.exp-info {
    margin-left: 10px;
    flex: 1;
    padding-top: 15px
}
.exp-title {
    font-size: 16px;
    color: #414755;
}
.exp-text {
    line-height: 1.6;
    margin-top: 10px;
    font-size: 14px;
    color: #6a7391;
}
</style>