<template>
    <div class="back-top" v-show="show" @click="backTop"></div>
</template>

<script>

export default {
    data () {
        return {
            show: false,
            timer: null
        }
    },
    methods: {
        backTop () {
            const that = this
            if (that.timer) cancelAnimationFrame(that.timer)
            let oTop = document.body.scrollTop || document.documentElement.scrollTop
            const diff = oTop / 1000
            that.timer = requestAnimationFrame(function fn() {
                oTop = document.body.scrollTop || document.documentElement.scrollTop
                if (oTop > 0) {
                    scrollTo(0, oTop - 20 * diff)
                    that.timer = requestAnimationFrame(fn)
                } else {
                    cancelAnimationFrame(that.timer)
                }    
            })
        }
    },
    mounted () {
        window.addEventListener('scroll', () => {
            const oTop = document.body.scrollTop || document.documentElement.scrollTop
            if (oTop >= 500) {
                this.show = true
            } else {
                this.show = false
            }
        })
    },
    beforeDestroy () {
        window.removeEventListener('scroll', () => {})
    }
}
</script>

<style scoped>
.back-top {
    position: fixed;
    z-index: 100;
    bottom: 50px;
    right: 6px;
    width: 56px;
    height: 56px;
    border-radius: 6px;
    background-size: 56px;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    box-shadow: 0 4px 6px 0 rgb(109 88 65 / 30%);
    cursor: pointer;
    background-image: url('../assets/y-zhiding.svg');
}
.back-top:hover {
    background-image: url('../assets/y-zhiding1.svg');
}
</style>