<!--  -->
<template>
    <div class="right-float" v-show="show">
        <div class="right-float-item right-float-help">
            <div class="right-float-tip">
                <div class="right-float-box">
                    <a href="#"></a>
                    <div class="float-arrow"></div>
                </div>
            </div>
        </div>
        <div class="right-float-item right-float-service">
            <div class="right-float-tip">
                <div class="right-float-box">
                    <a href="#"></a>
                    <div class="float-arrow"></div>
                </div>
            </div>
        </div>
        <div class="right-float-item right-float-app">
            <div class="right-float-tip">
                <div class="right-float-box">
                    <div class="float-qrcode"></div>
                    <div class="float-qrcode"></div>
                    <div class="float-arrow"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data () {
        return {
            show: false,
            timer: null
        }
    },
    methods: {
        backTop () {
            const that = this
            if (that.timer) cancelAnimationFrame(that.timer)
            let oTop = document.body.scrollTop || document.documentElement.scrollTop
            const diff = oTop / 1000
            that.timer = requestAnimationFrame(function fn() {
                oTop = document.body.scrollTop || document.documentElement.scrollTop
                if (oTop > 0) {
                    scrollTo(0, oTop - 20 * diff)
                    that.timer = requestAnimationFrame(fn)
                } else {
                    cancelAnimationFrame(that.timer)
                }    
            })
        }
    },
    mounted () {
        window.addEventListener('scroll', () => {
            const oTop = document.body.scrollTop || document.documentElement.scrollTop
            if (oTop >= 500) {
                this.show = true
            } else {
                this.show = false
            }
        })
    },
    beforeDestroy () {
        window.removeEventListener('scroll', () => {})
    }
}
</script>

<style scoped>
.right-float {
    position: fixed;
    z-index: 9998;
    top: 50%;
    right: 6px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 56px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.right-float-item {
    width: 56px;
    height: 56px;
    transition: all .4s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 3px;
    box-shadow: 5px 6px 10px 0 rgb(180 194 227 / 50%);
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    position: relative;
}
.right-float-help {
    background-image: url("../assets/y-bangzhu.svg");
}
.right-float-help:hover {
    background-image: url("../assets/y-bangzhu1.svg");
}
.right-float-service {
    background-image: url("../assets/y-kefu.svg");
}
.right-float-service:hover {
    background-image: url("../assets/y-kefu1.svg");
}
.right-float-app {
    background-image: url("../assets/y-app.svg");
}
.right-float-app:hover {
    background-image: url("../assets/y-app1.svg");
}
.right-float-tip {
    visibility: hidden;
    position: absolute;
    z-index: 9999;
    cursor: pointer;
    right: 50px;
    padding-right: 20px;
    top: 5px;
}
.right-float-item:hover .right-float-tip {
    visibility: visible;
}
.right-float-box {
    background-color: #fff;
    background-clip: padding-box;
    border-radius: 4px;
    box-shadow: 0 3px 6px -4px rgb(0 0 0 / 12%), 0 6px 16px 0 rgb(0 0 0 / 8%), 0 9px 28px 8px rgb(0 0 0 / 5%);
    padding: 12px 16px;
    width: 100px;
    text-align: center;
}
.right-float-box a {
    font-size: 14px;
    color: #b1987f;
    text-decoration-line: none;
}
.right-float-box a:hover {
    text-decoration-line: underline
}
.float-qrcode {
    width: 70px;
    height: 84px;
    background-color: rgba(0,0,0,0.2);
    margin-bottom: 4px;
}
.float-arrow {
    position: absolute;
    right: 16px;
    top: 20px;
    display: block;
    width: 8.48528137px;
    height: 8.48528137px;
    background: transparent;
    border-style: solid;
    border-width: 4.24264069px;
    border-top-color: #fff;
    border-right-color: #fff;
    border-bottom-color: transparent;
    border-left-color: transparent;
    box-shadow: 3px -3px 7px rgb(0 0 0 / 7%);
    transform: translateY(-50%) rotate(45deg)
}
</style>