<!--  -->
<template>
    <div class="home-banner">
        
        <transition-group name="list" tag="div" class="banners">
            <div class="banner-item" v-for="(item, i) in list" :key="item.id.toString() + i">
                <img :style="{visibility: activeIndex == i ? 'visible' : 'hidden'}" :class="activeIndex == i ? 'active-banner' : backClass" :src="item.pic" :key="i">
            </div>
        </transition-group>

        <div class="pages">
            <div class="pages-item left-pages" @click="goNext(true)">
                <Icon :icon="'iconzuo'"  />
            </div>
            <div class="pages-item right-pages" @click="goNext(false)">
                <Icon class="rotate-icon" :icon="'iconzuo'" />
            </div>
        </div>

        <div class="indicators">
            <div v-for="(item, i) in list" :key="i" class="indicators-item" @click="handleClick(item, i)">
                <img v-show="activeIndex != i" :src="imgs.nomal" alt="indicators">
                <img v-show="activeIndex == i" :src="imgs.active" alt="indicators">
            </div>
        </div>
    </div>
</template>

<script>
import Nomal from '../assets/dot-normal.png'
import Active from '../assets/dot-active.png'
import Banner1 from '../assets/banner1.jpg'
import Banner2 from '../assets/banner2.jpg'
import Banner3 from '../assets/banner3.jpg'
import Icon from './Icon'

export default {
    data () {
        return {
            imgs: {
                nomal: Nomal,
                active: Active,
                banner1: Banner1,
                banner2: Banner2,
                banner3: Banner3
            },
            activeIndex: 0,
            backClass: 'back-banner',
            list: [
                { id: 1, pic: Banner1 },
                { id: 2, pic: Banner2 },
                { id: 3, pic: Banner3 },
                { id: 4, pic: Banner1 },
                { id: 5, pic: Banner2 },
                { id: 6, pic: Banner3 },
                { id: 7, pic: Banner1 },
                { id: 8, pic: Banner2 },
                { id: 9, pic: Banner3 }
            ],
            interval: null,
            playKey: false
        }
    },
    watch: {
        activeIndex: function () {
            this.backClass = 'leave-banner'
            setTimeout(() => {
                this.backClass = 'back-banner'
            }, 500)
        }
    },
    components: {
        Icon
    },
    methods: {
        // 
        handleClick (item, i) {
            this.activeIndex = i
            this.autoPlay()
        },
        goNext (key) {
            this.autoPlay()
            this.nextBanner(key)
        },
        // 
        nextBanner (reverse) {
            if (this.playKey) return
            this.playKey = true
            setTimeout(() => {
                this.playKey = false
            }, 500)
            if (reverse) {
                this.activeIndex--
                if (this.activeIndex < 0) {
                    this.activeIndex = this.list.length - 1
                }
            } else {
                this.activeIndex++
                if (this.activeIndex >= this.list.length) {
                    this.activeIndex = 0
                }
            }
        },
        // 
        autoPlay () {
            if (this.interval) clearInterval(this.interval)
            this.interval = setInterval(() => {
                this.nextBanner(false)
            }, 5000)
        }
    },
    mounted () {
        this.autoPlay()
    },
    beforeDestroy () {
        if (this.interval) clearInterval(this.interval)
    }
}
</script>

<style scoped>
.home-banner {
    width: 100%;
    height: 600px;
    position: relative;
    z-index: 1;
    user-select: none;
    overflow: hidden;
}

.pages {
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 300px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9;
    visibility: hidden;
}
.home-banner:hover .pages {
    visibility: visible;
}
.pages-item {
    width: 39px;
    height: 80px;
    opacity: 0.5;
    color: #fff;
    background-color: #596077;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
.pages-item:hover {
    opacity: 0.7;
    cursor: pointer;
}
.rotate-icon {
    transform: rotate(180deg);
}
.indicators {
    position: absolute;
    width: 1200px;
    bottom: 15px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9;
}
.indicators-item {
    margin: 0 30px 0 0;
    width: 20px;
    height: 20px;
    transition: .2s;
    cursor: pointer;
    user-select: none;
}
.indicators-item img {
    width: 100%;
    height: 100%;
}
.banners {
    position: relative;
    height: 600px;
    z-index: 1;
}
.banner-item {
    width: 100%;
    height: 600px;
    position: absolute;
    top: 0;
    left: 0;
}
.banner-item img {
    width: 1920px;
    height: 100%;
    position: relative;
    left: 50%;
    transform: translateX(-50%) scale(0.8);
    z-index: 1;
}
.active-banner {
    z-index: 2;
    transform: translateX(-50%) scale(1)!important;
    opacity: 1;
    transition: all 0.6s ease;
}
.leave-banner {
    transform: translateX(-50%) scale(1.2)!important;
    opacity: 0;
    transition: all 0.4s ease;
}
.back-banner {
    transform: translateX(-50%) scale(0.8)!important;
    opacity: 0;
    transition: all 0s linear;
}
</style>